.listitems{
    background-color: rgb(70, 224, 224,0.3);
    border:1px solid black;
    width: 100%;

}
.listitems:hover {
    background-color: rgb(70, 224, 224,1);
}
.overdueitems {
    background-color: rgb(221, 150, 57, 0.3);
    border:1px solid black;
    width:100%;
    transition: all 0.5s;
}
.overdueitems:hover {
    background-color: rgb(221, 150, 57, 1);
    transform: scale(1.05);

}
